.icon-steam:before {
  content: "\E901";
}
.icon-menu:before {
  content: "\E900";
}


body {
  font-family: 'Source Sans Pro', sans-serif;
  margin: 0;
}

h1,h2,h3,h4,h5,h6{
  font-family: 'Fjalla One', Times, serif;
  font-weight: normal;
}

h1{
  margin: 0px;
  font-size: 40px;
  line-height: 1.5;
  letter-spacing: -.2px;
  color: white;
}

h1 strong{
  color: #026fff;
}

h2{
  color: #026fff;
  text-transform: uppercase;
  font-family: 'Source Sans Pro', sans-serif;
  font-weight: bold;
  font-size: 24px;
}

figure{
  margin: 0px;
}
.App-header{
    background-color: #1b2127;
    color: white;
    display: flex;
    justify-content: space-between;
    height: 70px;
    align-items: center;
    flex-wrap: wrap;
}

.App-header a{
    color: white;
    text-decoration: none;
    height: inherit;
    display: flex;
    align-items: center;
    padding: 0 10px;
}

.menu{
    height: inherit;
}

.App-header ol{
    display: flex;
    margin: 0px;
    padding: 0px;
    list-style: none;
    height: inherit;
}

.App-header ol li{
    height: inherit;
}

.container{
    max-width: 1200px;
    /*width: 100%;*/
    flex: 1 1;
    margin: 0 auto;
    display: inherit;
    justify-content: inherit;
    align-items: inherit;
    height: inherit;
    flex-wrap: inherit;
    padding: 0 0.5em;
}

.container.header{
    box-shadow: 1px 6px 15px -1px rgba(0,0,0,1);
    position: relative;
}

.burger-button{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background: rgba(0,0,0,0.8);
    display: none;
    line-height: 40px;
    text-align: center;
    position: fixed;
    z-index: 4;
    left: 5px;
    top: 5px;
    color:white;
}

@media screen and (max-width: 1024px){
    .container.header{
        z-index: 3;
    }
}

@media screen and (max-width: 766px){
    .App-header {
        display: block;
        height: auto;
    }

    .App-header ol{
        display: inline;
        display: initial;
    }

    .App-header ol li{
        height: 50px;
    }

    .App-header a{
        justify-content: center;
        height: 40px;
        font-size: 2em;
        text-decoration: underline;
        margin-bottom: 0.5em;
    }

    .App-header .logo{
        text-align: center;
    }

    .burger-button{
     display: block;
    }

    .menu{
        position: fixed;
        background: rgba(5, 111, 255, 0.8);
        top:0;
        left: -100vw;
        width: 100vw;
        bottom: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        -webkit-transition: .3s;
        transition: .3s;
    }

    .menu.is-active{
        left: 0;
    }
    
}
.Principal{
    height: 300px;
    /*background-image: url('../images/hero.jpg');*/
    /*background-color: #1b2127;*/
    /*background-size: 500px 300px;*/
    /*background-repeat: no-repeat;*/
    /*background-position-x: right; */
    display: flex;
    align-items: center;
    justify-content: space-between;
    box-shadow: none;
    flex-wrap: wrap;
}

#principal{
    background-color: #1b2127;
}

.hero{
    object-fit: cover;
}

@media screen and (max-width:1023px){
    .Principal{
        height: auto;
        position: relative;
        overflow: hidden;
        padding: 2em;
    }

    .hero{
        position: absolute;
        z-index: 1;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        width: 100%;
    }

    h1{
        font-size: 2em;
        z-index: 2;
    }
}

@media screen and (max-width:480px){
    h1{
        font-size: 1.2em;
    }
}



.Portafolio-details{
    background: #fafafa;
}
.Portafolio{
    background: #fafafa;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
}

.Portafolio-details h2{
    margin:0px;
    padding: 20px;
    padding-bottom: 15px;
}

.project{
    box-shadow: 3px 5px 33px#888888;
    border-radius: 10px;
    margin: 10px;
    margin-bottom: 15px;
    padding: 20px;
    background: white;
    flex-shrink: 0;
    align-items: center; 
    display: flex;
    flex-wrap: wrap;
    -webkit-transition: 300ms;
    transition: 300ms;
}

.project:hover 
{
    -webkit-transform: scale(1.01);
            transform: scale(1.01);
}

.project-details {
    width: 450px;
    -webkit-transition: 300ms;
    transition: 300ms;
}

.project-imageContainer{
    width: 450px;
    margin: auto;
    padding: 20px;
    object-fit: cover;
    -webkit-transition: 300ms;
    transition: 300ms;
}

.project-title{
    font-size: 30px;
    margin-top: 0px;
    margin-bottom: 10px;
}

.project-technology,.project-date,.project-url{
    margin: 10px 0px;

}

.project-description{
    font-size: 20px;
}

.project strong{
    font-family: 'Fjalla One', Times, serif;
}

@media screen and (max-width: 1023px){
    .project{
        display: block;
    }
    .project-details {
        width: 650px;
    }
}

@media screen and (max-width: 767px){
    .project-details {
        width: 420px;
        font-size: 16px;
    }

    .project-imageContainer{
        width: 380px;
        margin: auto;
        object-fit: cover;
    }

    .project-imageContainer img{
        width: 380px;
    }

    .project-title{
        font-size: 1.5em;
        margin-bottom: 0.3em;
    }

    .project-url{
        font-size: 1em;
    }

    .project-technology{
        font-size: 0.7em;
    }

    .project-date{
        font-size: 1em;
    }

    .project-description{
        font-size: 0.8em;
    }

    .project-technology .project-date .project-url{
        margin: 0.3em 0;
    }

}

@media screen and (max-width: 479px){
    .project{
        width: 260px;
    }
    .project-details {
        width: 260px;
        font-size: 12px;
    }

    .project-imageContainer{
        width: 220px;
        margin: auto;
        object-fit: cover;
    }

    .project-imageContainer img{
        width: 220px;
    }

    .Portafolio-details h2{
        font-size: 1em;
    }
}

.Experiencia-details{
    background: #fafafa;
}

.Experiencia-details h2{
    margin:0px;
    padding: 20px;
    padding-bottom: 15px;
}

.event{
    margin: 10px;
    width: 480px;
    background:#f4fbff;
    border-radius: 10px;
    flex-shrink: 0;
    overflow: hidden;
    box-shadow: 3px 5px 33px#888888;
    -webkit-transition: 300ms;
    transition: 300ms;
}

.event:hover 
{
    -webkit-transform: scale(1.01);
            transform: scale(1.01);
}

.Experiencia{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
}

.event img{
    width: 480px;
    height: 200px;
    object-fit: cover;
}

.event-details{
    margin: 20px 40px;
    background: white;
    margin-top: -40px;
    position: relative;
    padding: 20px;
    text-align: center;
}

.event-description{
    text-align: left;
}

.event-url{
    color: #056fff;
    border: 1px solid;
    padding: 5px 20px;
    text-decoration: none;
    border-radius: 5px;
}

@media screen and (max-width: 767px){
    .event {
        width: 460px;
        font-size: 16px;
    }

    .event img{
        width: 460px;
        height: 180px;
    }

}

@media screen and (max-width: 479px){
    .event-details{
        margin: 0.5em 2em;
        background: white;
        margin-top: -2em;
        position: relative;
        padding: 20px;
        text-align: center;
    }

    .event {
        width: 300px;
        font-size: 12px;
    }

    .event img{
        width: 300px;
        height: 100px;
    }

    .event-title{
        font-size: 1.2em;
    }

    .event-description{
        font-size: 0.8em;
    }

    .Experiencia-details h2{
        font-size: 1em;
    }
}
